@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');


#app {
  height: 100%;

}

ul,
li,
ol {
  text-decoration: none;
  list-style-type: none;
}

html,
body {
  position: relative;
  height: 100%;
  font-family: 'Anton', sans-serif;
}

body {
  background: rgb(241, 241, 241);
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.accordion {
  width: 100%;
  /* height: 500px; */
  background-color: transparent;
  border: 1px solid transparent;
  position: fixed;
  z-index: 200;
  /* position:sticky; */
  margin: -1px;
}

.card {
  width: 100%;
  /* height: 500px; */
  background-color: transparent;
  border: 1px solid transparent;
  /* margin-top: -3px; */
  z-index: -1;
  /* transition: 5s; */
  margin: -1px;
}


.card-header:first-child {
  width: 20%;
  height: 100px;
  position: absolute;
  z-index: 150;
  border: 1px solid transparent;
  background-color: transparent;
  /* top: 10%; */
  left: 50%;
  padding-top: 55px;
  transform: translate(-50%, -10%);


}


.card-header {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  cursor: pointer;


}

.card-header button {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  background-color: transparent !important;
  cursor: pointer;

}

.logo {
  width: 70px;
  height: 70px;
  margin-top: 30px;
  filter: brightness(10000) invert(1);
  cursor: pointer;

}

.logo-after {
  width: 70px;
  height: 70px;
  margin-top: 30px;
  filter: none;
  cursor: pointer;
}

.acoordion-collapse {
  width: 100%;
  height: 500px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  background-color: transparent;

}

.card-body {
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  background-color: rgb(26, 26, 26);
  flex-direction: column;

}

