  background-color: rgb(39, 39, 39);
  width: 100%;
  height: 50vh;
  display: none;
  z-index:101;
} */

#menu-top {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 0 0 20px;
}

#menu-top img {
  width: 60px;
  height: 20px;
  filter: brightness(10000) invert(1);
  margin-top: -10px;
  margin-right: 20px;
  cursor: pointer;

}

#menu-top p {
  color: rgb(97, 97, 97);
  font-weight: bolder;
  margin-right: 20px;
  cursor: pointer;
}

#menu-top p:hover {
  color: white;
  /* z-index:200; */
  border-bottom: 3px solid red;
}

#menu-bottom {
  margin-top: 150px;

}

#menu-bottom ul {
  display: flex;
  justify-content: center;
  align-items: center;

}

#menu-bottom ul li img {
  width: 200px;
  height: 200px;
  cursor: pointer;
  transform: scale(1.0);
  transition: transform .3s;

}

#menu-bottom ul li img:hover {
  transform: scale(1.2);
  transition: transform .3s;

}


.mySwiper {
  width: 100%;
  height: 1700px;
  /* background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRzs-vWeFSxOMO-6iZSsQkdsO6cJn0t2BnyMg_bVsg-0QK2Lz-JnZoh4hYYZjC2oFbE_Wg&usqp=CAU"); */

  background-image: url("https://static.nike.com/a/images/81c12884-d362-4991-be27-aea6a2d48aea/air-jordan-35.jpg");

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
}

.swiper {
  width: 100%;
  height: 1700px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background-color: transparent;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.txt {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* flex-direction: column; */
  color: white;
  position: absolute;
  z-index: 180;
  font-size: 14px;
  margin-top: 15px;
  opacity: 1;
  transition: 0.5s;
}

.txt-after {
  opacity: 0;
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.txt ul li:nth-child(1) {
  font-weight: bolder;
}

.txt ul {
  width: 200px;
  height: 50px;
  text-align: center;
}

.txt ul li {
  text-align: center;
}

.txt p {
  width: 300px;
  height: 150px;
  font-size: 100px;
  text-align: center;
  font-weight: bolder;
  /* font-family: 'Lucida Sans'; */
}

#img1 img:nth-child(1) {
  width: 1100px;
  height: 1100px;
  margin: 0 auto;
  position: absolute;
  z-index: 100;
  top: 70px;
}

#img1 img:nth-child(2) {
  width: 1100px;
  height: 1100px;
  margin: 0 auto;
  position: relative;
  top: -222px;

}

.container {
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 80%;
  height: 600px;
  margin: 0 auto;
  z-index: 300;
  color: white;
  /* border: 1px solid white; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 1s;

}

.container-after {
  opacity: 1;
  transition: 1s;

}

.row {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.col-md-2 p:nth-child(1) {
