.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: grab;
}

/* .swiper-button-next{
  
} */

.swiper-button-next {
  text-shadow: 3px 3px 20px #ffffff,
    -2px 1px 30px #ffffff;
  position: absolute;
  top: 30%;
  right: 0;
  transform: translate(-50%, -50%);
}

.swiper-button-next::after {
  content: '→';
  /* display:block; */
  font-size: 20px;
  /* background: url("img/right.png") center no-repeat; */
  /* background-size: contain; */
  color: white;

}

.swiper-button-prev {
  text-shadow: 3px 3px 20px #ffffff,
    -2px 1px 30px #ffffff;
  position: absolute;
  top: 30%;
  left: 25px;
  transform: translate(-50%, -50%);
}

.swiper-button-prev::after {
  content: '←';
  font-size: 20px;
  color: white;

}

